.main-box {
  padding-top: 61px;
  @include max-screen(576px) {
    padding-top: 35px;
  }
  .main-box__wrap {
    display: flex;
    z-index: 1;
    position: relative;
    @include max-screen(992px) {
      flex-direction: column;
    }
    .main-box__left {
      flex-basis: 50%;
      @include max-screen(1200px) {
        padding-right: 15px;
      }
      @include max-screen(992px) {
        padding-right: 0;
      }
      .main-box__left_text {
        font-size: 19.17px;
        margin-top: 25px;
        margin-bottom: 27px;
      }
      .main-box__left_text1 {
        line-height: 1.4em;
      }
      .main-box__left_wrap {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        margin-top: 45px;
        @include max-screen(992px) {
          justify-content: center;
        }
        .main-box__left_img {
          width: 135px;
          height: 45px;
          margin-right: 23px;
        }
        .main-box__left_img1 {
          width: 135px;
          height: 45px;
          margin-right: 9px;
        }
      }
    }
    .main-box__right {
      flex-basis: 50%;
      z-index: 1;
      position: relative;
      @include max-screen(1200px) {
        padding-left: 15px;
      }
      @include max-screen(992px) {
        padding-left: 0;
        margin: 30px auto 0;
      }
      .main-box__right_img {
        width: 502px;
        height: 336px;
        margin-left: auto;
        display: flex;
        align-items: center;
        position: relative;
        z-index: 1;
        a {
          display: block;
          margin: auto;
          padding-top: 18px;
          max-width: 462px;
        }
        @include max-screen(576px) {
          width: 100%;
        }
        .main-box__right_img-element {
          position: relative;
          z-index: 1;
          @include max-screen(576px) {
            display: none;
          }
          &::before {
            content: '\f10a';
            font-family: Flaticon;
            position: absolute;
            color: $btn;
            top: -167px;
            left: -8px;
            font-size: 30px;
            font-weight: 100;
            transform: rotate(-135deg);
            display: block;
            z-index: 10;
          }
          &::after {
            content: '\f10a';
            font-family: Flaticon;
            position: absolute;
            color: $btn;
            bottom: -188px;
            z-index: 10;
            right: -22px;
            font-size: 30px;
            font-weight: 100;
            transform: rotate(135deg);
            display: block;
          }
        }
        .main-box__right_img-element1 {
          position: relative;
          z-index: 1;
          @include max-screen(576px) {
            display: none;
          }
          &::before {
            content: '\f10a';
            font-family: Flaticon;
            position: absolute;
            color: $btn;
            top: -166px;
            left: -19px;
            font-size: 30px;
            font-weight: 100;
            transform: rotate(-45deg);
            display: block;
            z-index: 100;
          }
          &::after {
            content: '\f10a';
            font-family: Flaticon;
            position: absolute;
            color: $btn;
            bottom: -187px;
            right: -12px;
            font-size: 30px;
            font-weight: 100;
            transform: rotate(45deg);
            display: block;
            z-index: 100;
          }
        }
      }
    }
  }
}

