            position: absolute;
            font-family: Flaticon;
            color: $btn;
            font-size: 15px;
            transform: rotate(-135deg);
            bottom: 63px;
            right: 212px;
          }
          &::before {
            content: '\f10a';
            position: absolute;
            font-family: Flaticon;
            color: $btn;
            font-size: 15px;
            transform: rotate(135deg);
            bottom: 12px;
            right: 212px;
          }
        }
      }
    }
  }
}

